import React from 'react';
import { clsx } from '../../../libs/clsx';
import { mergeProps } from '../../../libs/merge-props';
import { IntrinsicElementProps } from '../../../types/utility';

import styles from './skeleton.less';

interface SkeletonProps extends IntrinsicElementProps<'span'> {
  count?: number;
  duration?: number;
  shape?: 'circle' | 'pill';
  width?: React.CSSProperties['width'];
  height?: React.CSSProperties['height'];
}

/**
 * Renders an animated loading skeleton that automatically adapts to the current
 * font-size and line-height.
 *
 * Based on `react-loading-skeleton`.
 *
 * @see the LICENSE file at the root of this source tree:
 *   https://github.com/dvtng/react-loading-skeleton
 *
 * Modifications from original source:
 *   - Removes usage of `emotion` for CSS.
 *   - Adds `shape` prop to control the general shape of the skeleton.
 *   - Adds default TypeScript support.
 */
export const Skeleton: React.FC<SkeletonProps> = (props) => {
  const { count = 1, duration = 1200, shape, width, height, ...otherProps } = props;

  const elements = [];

  for (let i = 0; i < count!; i++) {
    elements.push(
      <span
        key={i}
        {...mergeProps(
          {
            className: clsx(styles.item, shape === 'circle' && styles.circle, shape === 'pill' && styles.pill),
            style: {
              width,
              height,
              animation: `${styles.skeletonAnim} ${duration}ms ease-in-out infinite`,
            },
          },
          otherProps,
        )}
      >
        &zwnj;
      </span>,
    );
  }

  return <span className={clsx(styles.Skeleton)}>{elements}</span>;
};

Skeleton.displayName = 'Skeleton';
